import uniFab from '@/components/uni-fab/uni-fab.vue';
import huiziDeatil from '@/components/huizi-deatil/huizi-detail.vue';
import navBar from "@/components/zhouWei-navBar";
import publicFnc from '@/static/js/public.js';
export default {
	data() {
		return {
			title: 'Hello',
			pattern: {
				color: '#3c3e49',
				selectedColor: '#ea6566',
				backgroundColor: '#ea656600',
				buttonColor: '#ea6566'
			},
			content: [{
					iconPath: "/static/images/item_2.svg",
					selectedIconPath: "/static/images/item_2.svg",
					text: '',
					model: 'home',
					active: false
				},
				{
					iconPath: "/static/images/rili_1.svg",
					selectedIconPath: "/static/images/hover_rili_1.svg",
					text: '',
					model: 'calendar',
					active: false
				}
			],
			huiziData: [],
			today_money:{},
			tomonth_money:{},
			horizontal: 'right',
			vertical: 'bottom',
			direction: 'vertical',
			popMenu: true
		};
		
	},
	filters:{
		e(val){ return val || '0'}
	},
	onLoad() {
		this.initToday();
	},
	methods: {
		trigger(e) {
			let model = e.item.model;
			switch (model) {
				case 'calendar':
					uni.navigateTo({
						url: '/pages/calendar/index',
						success: res => {},
						fail: () => {},
						complete: () => {}
					});
					break;
				default:
				
			}
		},
		initToday() {
			let date = new Date();
			console.log(date )
			let y = date.getFullYear();
			let m = date.getMonth();
			let d = date.getDate();
			let lunar = publicFnc.calendar.solar2lunar(y, m + 1, d);
			let today_data = {
				date: d,
				lunar: lunar.IDayCn,
				month: m,
				year:y
			}
			this.huiziData =[];
			this.huiziData =  publicFnc.toDay.getToDayData( today_data );
			let tomonth_money = publicFnc.toDay.getToMonthData( today_data );
			this.today_money = this.getMoney( this.huiziData);
			this.tomonth_money = this.getMoney( tomonth_money );
		},
		getMoney( data ){
			let len = [...data].length;
			let money= 0 ;
			let uncalled = 0 ;
			for( let i = 0; i<len ;i++){
				if( data[i].huizi_arr[0].cost == 0){
					uncalled += 1 ;
				}
				money += Number( data[i].huizi_arr[0].cost );
			}
			let money_data ={
				uncalled:uncalled,
				money:money,
				expect:len
			}
			
			return money_data ;
		}
	},
	components: {
		uniFab,
		huiziDeatil,
		navBar
	}
};
